*{
    padding: 2px;
    margin: 0;
    box-sizing: border-box;
}
body{
    background-color: rgb(17, 14, 63);
}
.prime1{
    background-color: rgb(17, 14, 63);
}
.prime2{
    background-color: rgb(157, 222, 255);
}

.allfont{
    font-weight: bolder;
    font-style: oblique;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    
}
.data .first .item {
    background: linear-gradient(10deg, #1b59e8 10%, #d0009f 81%);
    color: white;
        
}
.main{
    border: 1px solid black;
    color: white;
    text-align: center;
}
::-webkit-scrollbar{
    width: 12px;

}
::-webkit-scrollbar-thumb{
    background: linear-gradient(10deg, #1b59e8 10%, #d0009f 81%);
    /* background: linear-gradient(transparent,#30ff00); */
    border-radius: 5px;
}
::-webkit-scrollbar-thumb:hover{
    background: linear-gradient(transparent,#30ff00);
}